<template>

  <div class="consoleframe">

    <!-- <div class="taskbarframe"> -->
    <TaskBar id="taskbar"
      :solid-bg="$store.getters.activeApp !== 'launcher'"
      @about="aboutActive = true"
    />

    <!-- App View -->
    <AppView/>

    <!-- Global Display Elements -->
    <LoginView/>
    <Toast/>
    <About :active="aboutActive"
      @close="aboutActive = false"
    />

    <!-- GA tracking code on remote server -->
    <iframe id="gaFrame" src="http://s3-us-west-2.amazonaws.com/onion-cdn/os/GA/index.html"></iframe>

  </div>
</template>
<script>

import TaskBar from '@/components/TaskBar'
import AppView from '@/components/AppView'
import LoginView from '@/components/LoginView'
import Toast from '@/components/Toast'
import About from '@/components/About'

export default {
  mqtt: {
    '#': function (data, topic) {
      console.log(`topic: ${topic}, msg: ${data}`)
      // console.log(data)
      this.$store.dispatch('mqttMessage', {topic: topic, data: data})
    }
  },
  name: 'ConsoleFrame',
  data () {
    return {
      aboutActive: false
    }
  },
  components: {
    TaskBar,
    AppView,
    LoginView,
    Toast,
    About
  },
  created () {
    // mqtt subscription
    // this.$mqtt.subscribe('/console/')
    this.$mqtt.subscribe('/console/+')

    // Check internet status
    var checkInternet = function (e) {
      if (e && e.type === 'offline') {
        this.$store.commit('updateInternetStatus', false)
      } else {
        this.$store.dispatch('checkInternet')
        // TODO: use notification instead of tooltip
      }
    }.bind(this)
    checkInternet()
    // Re-Check when network status change
    window.addEventListener('online', checkInternet)
    window.addEventListener('offline', checkInternet)

    // Login
    // console.log('Authenticated: ' + this.$store.getters.isAuthenticated)
    // if (!this.$store.getters.isAuthenticated) {
    //   this.$store.dispatch('login', {
    //     username: 'root',
    //     password: 'onioneer'
    //   })
    //     .then(resp => {
    //       console.log('login success!' + this.$store.getters.isAuthenticated)
    //       this.$store.dispatch('request', {
    //         object: 'onion',
    //         method: 'wifi-scan',
    //         params: {
    //           device: 'ra0'
    //         }
    //       })
    //         .then(resp => {
    //           console.log(resp)
    //         })
    //     })
    //     .catch(resp => {
    //       console.log('login failed:' + resp)
    //     })
    // }

    this.$store.dispatch('launchApp', {appId: 'launcher'})
    this.$store.dispatch('getDeviceInfo')
    this.$store.dispatch('initCDK')
    this.$store.dispatch('refreshAppList')
  }
}
</script>

<style scoped>
.consoleframe {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  background-image: url("~@/assets/img/bg.jpg");
  background-size: cover;
}

#gaFrame {
  width: 0;
  height: 0;
  border: 0;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 768px) {
    .consoleframe {
        flex-direction: column;
    }
}

</style>



