import Vue from 'vue'
import Vuex from 'vuex'
import ubus from '../api/ubus'
import isOnline from 'is-online'

Vue.use(Vuex)

window.console.makeId = function () {
  return Math.random().toString(36).substring(2)
}

var devMode = process.env.NODE_ENV === 'development'
var devHost = process.env.DEV_HOST
var hostName = `${devMode ? devHost : window.location.hostname}`
var appPath = '/apps/'

var defaultToken = '00000000000000000000000000000000'

var store = new Vuex.Store({
  // variables
  state: {
    system: {
      online: false
    },
    device: { },
    rpc: {
      username: '',
      password: '',
      token: sessionStorage.getItem('_onion_console_session_token') || defaultToken,
      // url: 'http://omega-2767.local/ubus'
      url: `http://${devMode ? devHost : window.location.hostname}/ubus`,
      heartbeatHandel: null
    },
    mqtt: {
      subscription: {}
    },
    apps: {
      active: 'launcher',
      running: [],
      available: {
        launcher: {
          id: 'launcher',
          type: 'AppLauncher',
          name: 'Launcher',
          background: true
        },
        // 'home-app': {
        //   id: 'home-app',
        //   type: 'IframeApp',
        //   name: 'Onion Site',
        //   icon: 'static/img/icons/home.svg',
        //   params: {url: 'https://onion.io'}
        // },
        'doc-app': {
          id: 'doc-app',
          type: 'IframeApp',
          name: 'Onion Docs',
          icon: 'static/img/icons/docs.svg',
          params: {
            url: 'https://docs.onion.io',
            internet: true
          }
        },
        // 'nfc-rfid-app': {
        //   id: 'nfc-rfid-app',
        //   type: 'IframeApp',
        //   name: 'NFC-RFID Expansion',
        //   icon: 'static/img/icons/nfc-rfid-app.svg',
        //   params: {url: '/apps/nfc-rfid-exp'}
        //   // params: {url: 'http://localhost:8081'}
        // },
        'terminal-app': {
          id: 'terminal-app',
          type: 'IframeApp',
          name: 'Terminal',
          icon: 'static/img/icons/terminal.svg',
          params: {url: `http://${devMode ? devHost : window.location.hostname}:4200`}
        },
        'editor-app': {
          id: 'editor-app',
          type: 'IframeApp',
          name: 'Code Editor',
          icon: 'static/img/icons/code.svg',
          params: {url: `${devMode ? 'http://' + devHost : ''}` + '/apps/editor/'}
        }
        // 'legacy-console-app': {
        //   id: 'legacy-console-app',
        //   type: 'ExternalApp',
        //   name: 'Legacy Console',
        //   icon: 'static/img/icons/old-console-app.svg',
        //   params: {url: '/console'}
        // }
        // 'preferences-app': {
        //   id: 'preferences-app',
        //   type: 'PreferencesApp',
        //   name: 'Preferences',
        //   icon: 'static/img/icons/preference.svg'
        // }
        // 'file-browser-app': {
        //   id: 'file-browser-app',
        //   type: 'IframeApp',
        //   name: 'Tiny File Manager',
        //   icon: 'static/img/icons/folder.svg',
        //   params: {url: 'http://omega-2767.local/tinyfilemanager.php'}
        // },
        // 'cron-manager-app': {
        //   id: 'cron-manager-app',
        //   type: 'IframeApp',
        //   name: 'Cron Task Manager',
        //   icon: 'static/img/icons/tasks.svg',
        //   params: {url: 'http://omega-2767.local/CronWeb/#new'}
        // },
        // 'log-viewer-app': {
        //   id: 'log-viewer-app',
        //   type: 'IframeApp',
        //   name: 'Log Viewer',
        //   icon: 'static/img/icons/log.svg',
        //   params: {url: 'http://omega-2767.local/tail/Log.php'}
        // }
      }
    }
  },
  // functions that change the state - must be synchronous
  mutations: {
    registerUser (state, {username, password}) {
      state.rpc.username = username
      state.rpc.password = password
    },
    registerSession (state, token) {
      if (token) {
        state.rpc.token = token
        sessionStorage.setItem('_onion_console_session_token', token)
      } else {
        state.rpc.token = defaultToken
        sessionStorage.removeItem('_onion_console_session_token')
      }
    },
    registerAppHandler (state, appId, callback) {

    },
    launchApp (state, appId) {
      var app = state.apps.available[appId]

      var appExist = false
      for (var i = 0; i < state.apps.running.length; i++) {
        var runningApp = state.apps.running[i]
        if (runningApp.id === app.id) {
          appExist = true
          break
        }
      }

      if (!appExist) {
        state.apps.running.push(app)
        if (app.type !== 'IframeApp') {
          Vue.component(app.type, () => import('@/components/' + app.type))
        }
      }
      state.apps.active = appId
    },
    closeApp (state, appId) {
      for (var i = 0; i < state.apps.running.length; i++) {
        var runningApp = state.apps.running[i]
        if (runningApp.id === appId) {
          state.apps.running.splice(i, 1)
          break
        }
      }
    },
    updateInternetStatus (state, isOnline) {
      state.system.online = isOnline
    },
    mqttSubscribe (state, {appUid, topic}) {
      if (topic in state.mqtt.subscription) {
        state.mqtt.subscription[topic].push(appUid)
      } else {
        state.mqtt.subscription[topic] = [appUid]
      }
    },
    deviceInfo (state, device) {
      state.device = device
    },
    updateAppList (state, apps) {
      for (var i = 0; i < apps.length; i++) {
        var app = apps[i]
        app.type = app.type || 'IframeApp'
        app.icon = 'http://' + hostName + appPath + app.id + '/' + app.icon

        app.params = app.params || {}
        if (app.type === 'IframeApp' && app.params.url === undefined) {
          app.params.url = 'http://' + hostName + appPath + app.id
        }
        // app.params.url = app.params.url.replace('http://', '')
        app.params.url = app.params.url.replace('{{host}}', hostName)
        Vue.set(state.apps.available, app.id, app)
      }

      // Include Construction Site App in Dev Model
      if (devMode) {
        Vue.set(state.apps.available, 'construction-site',
          {
            id: 'construction-site',
            type: 'IframeApp',
            name: 'Site 8081',
            icon: 'static/img/icons/crane.svg',
            params: {url: 'http://localhost:8081'}
          })
      }
    }
  },
  // functions that commit mutations - can be asynchronous
  actions: {
    login ({commit, state, dispatch}, {username, password}) {
      return new Promise(function (resolve, reject) {
        ubus.request(
          state.rpc.url,
          state.rpc.token,
          'session',
          'login',
          {
            'username': username,
            'password': password
          }
        )
          .then(resp => {
            // login success
            // console.log('login success:', resp)
            commit('registerUser', {
              'username': username,
              'password': password
            })
            commit('registerSession', resp.ubus_rpc_session)
            dispatch('getDeviceInfo')
            dispatch('refreshAppList')
            dispatch('startHeartbeat')
            resolve(resp)
          })
          .catch(resp => {
            console.log('login failed')
            reject(resp)
          })
      })
    },
    logout ({commit}) {
      commit('registerUser', {
        'username': '',
        'password': ''
      })
      commit('registerSession', null)
    },
    getDeviceInfo ({commit, state, dispatch, getters}) {
      if (!getters.isAuthenticated) { return }

      dispatch('request', {
        object: 'system',
        method: 'board',
        params: {}
      }).then(resp => {
        commit('deviceInfo', resp)
      })
    },
    request ({commit, state, dispatch, getters}, {object, method, params}) {
      // TODO: check if authenticated
      return new Promise(function (resolve, reject) {
        if (getters.isAuthenticated) {
          ubus.request(
            state.rpc.url,
            state.rpc.token,
            object,
            method,
            params
          )
            .then(resp => {
              // request success
              resolve(resp)
            })
            .catch(resp => {
              // request failed
              console.log(resp)
              if (resp && resp.code === ubus.errorCode.AccessDenied) {
                dispatch('logout')
                console.log('Session expired')
              }
              reject(resp)
            })
        } else {
          reject(Error('Not authenticated'))
        }
      })
    },
    launchApp ({commit}, {appId}) {
      commit('launchApp', appId)
    },
    closeApp ({commit, state}, {appId}) {
      commit('closeApp', appId)
      commit('launchApp', 'launcher')
    },
    notifyApp ({commit}, {appUid, eventId, data}) {
      console.log('on notify app')
      var event = new Event(`app-${appUid}`)
      event.data = {
        content: data
      }
      event.name = eventId
      window.dispatchEvent(event)
    },
    checkInternet ({commit}) {
      isOnline().then(online => {
        commit('updateInternetStatus', online)
      })
    },
    initCDK ({dispatch}) {
      window.addEventListener('message', function (e) {
        var data = e.data
        if (typeof data.event !== 'undefined' &&
            typeof data.instance !== 'undefined' &&
            typeof data.eventId !== 'undefined' &&
            typeof data.content !== 'undefined'
        ) {
          if (data.event.indexOf('Onion.CDK') > -1) {
            // debug
            // console.toast('test toast')
            var requestType = data.event.replace('Onion.CDK.', '')
            switch (requestType) {
              case 'Service':
                dispatch('handleService', (data))
                  .then(resp => {
                    var event = new Event(`app-${data.instance}`)
                    event.data = {
                      content: {
                        command: data.content.command,
                        name: data.content.service,
                        result: resp
                      }
                    }
                    event.name = data.event
                    window.dispatchEvent(event)
                  })
                break
              case 'Subscribe':
                dispatch('mqttSubscribe', {
                  topic: data.content.topic,
                  appUid: data.instance
                })
                break
              case 'Command':
                var eventId = 'Onion.CDK.Command'
                dispatch('handleCommand', {cmd: data.content.cmd, params: data.content.params})
                  .then(resp => {
                    dispatch('notifyApp', {appUid: data.instance, eventId: eventId, data: {cmd: data.content.cmd, resp: resp.stdout}})
                  })
                break
              case 'Toast':
                console.toast(data.content.message)
                break
              default:
                console.log('unrecognized CDK event: ' + data.event)
                break
            }
          }
        }
      }, false)
    },
    handleService: function ({dispatch}, data) {
      return new Promise(function (resolve, reject) {
        if (data.content.command === 'start' || data.content.command === 'stop') {
          dispatch('request', {
            object: 'file',
            method: 'exec',
            params: {
              command: '/etc/init.d/' + data.content.service,
              params: [
                data.content.command
              ]
            }
          })
            .then(resp => {
              // console.log('Service ' + data.content.service + ' accepted ' + data.content.command + ' command')
              resolve(true)
            })
            .catch(resp => {
              // console.log('Service start failed!')
              resolve(false)
            })
        } else if (data.content.command === 'list') {
          dispatch('serviceStatus')
            .then(resp => {
              // console.log(resp)
              if (resp[data.content.service]) {
                resolve(true)
              } else {
                resolve(false)
              }
            })
        } else {
          reject(Error('Invalid input'))
        }
      })
    },
    serviceStatus ({dispatch}) {
      return dispatch('request', {
        object: 'service',
        method: 'list',
        params: {}
      })
    },
    handleCommand ({dispatch}, {cmd, params}) {
      console.log('handle Command')
      return dispatch('request', {
        object: 'file',
        method: 'exec',
        params: {command: cmd, params: params}
      })
    },
    mqttSubscribe ({commit, dispatch}, {appUid, topic}) {
      commit('mqttSubscribe', {appUid, topic})
    },
    mqttMessage ({state}, {topic, data}) {
      if (topic in state.mqtt.subscription) {
        for (var appUid of state.mqtt.subscription[topic]) {
          var event = new Event(`app-${appUid}`)
          event.data = {
            content: {
              topic: topic,
              content: data.toString()
            }
          }
          event.name = 'Onion.CDK.Message'
          window.dispatchEvent(event)
        }
      }
    },
    refreshAppList ({getters, commit, dispatch}) {
      if (!getters.isAuthenticated) { return }
      dispatch('request', {
        object: 'onion-os',
        method: 'app-list',
        params: {}
      }).then(resp => {
        if (resp.exists) {
          commit('updateAppList', resp.apps)
        }
      })
    },
    startHeartbeat ({state, dispatch}) {
      // console.log('startHeartbeat')
      if (state.rpc.heartbeatHandel) {
        clearInterval(state.rpc.heartbeatHandel)
        state.rpc.heartbeatHandel = null
      }
      state.rpc.heartbeatHandel = setInterval(function () {
        dispatch('request', {
          object: 'session',
          method: 'access',
          params: {}
        })
      }, 60000)
    }
  },
  // provide access to state objects
  getters: {
    isAuthenticated (state) {
      return (state.rpc.token !== defaultToken)
    },
    isOnline (state) {
      return state.system.online
    },
    sessionToken (state) {
      return state.rpc.token
    },
    requestUrl (state) {
      return state.rpc.url
    },
    activeApp (state) {
      return state.apps.active
    },
    runningApps (state) {
      var result = []
      for (var i = 0; i < state.apps.running.length; i++) {
        var app = state.apps.running[i]
        if (app.background === undefined) {
          result.push(app)
        }
      }
      return result
    },
    availableApps (state) {
      var result = []
      for (var a in state.apps.available) {
        var app = state.apps.available[a]
        if (app.background === undefined) {
          result.push(app)
        }
      }
      return result
    },
    deviceInfo (state) {
      // console.log(state.device)
      return state.device
    }
  }
})

export default store



