import axios from 'axios'
axios.defaults.headers.post['Content-Type'] = 'application/json'

export default {
  request (url, token, pkg, fn, params) {
    return new Promise(function (resolve, reject) {
      axios.post(url, {
        'jsonrpc': '2.0',
        'id': 0,
        'method': 'call',
        'params': [
          token,
          pkg,
          fn,
          params
        ]
      })
        .then(response => {
          // console.log(response)
          if (response.data) {
            if (response.data.result && Array.isArray(response.data.result) && typeof response.data.result[0] !== 'undefined' && response.data.result[0] === 0) {
              // console.log('ubus success!')
              // console.log(response.data.result[1])
              resolve(response.data.result[1])
            } else {
              console.log('ubus error!')
              if (response.data.error) {
                // console.log('code ' + response.data.error.code || -1 + ' message ' + response.data.error.message || '')
                reject(response.data.error)
              } else if (response.data.result && Array.isArray(response.data.result) && typeof response.data.result[0] !== 'undefined') {
                // console.log('code ' + response.data.result[0])
                reject(response.data.result[0])
              }
            }
          }
        })
        .catch(e => {
          console.error(e)
          // this.errors.push(e)
          reject(e)
        })
    })
  },
  errorCode: {
    AccessDenied: -32002
  }
}



