<template>

  <div class="modal modal-sm" :class="{active: !$store.getters.isAuthenticated}" >
    <div class="modal-container">

      <!-- Header -->
      <div class="modal-header">
        <img  src="static/img/OnionOS.svg">
      </div>

      <!-- Body -->
      <div class="modal-body" @keyup.enter="login">
        <div class="form-group">
          <label class="form-label" for="input-username">Username</label>
          <input class="form-input" type="text" id="input-username" v-model="username">
        </div>
        <div class="form-group">
          <label class="form-label" for="input-password">Password</label>
          <input class="form-input" type="password" id="input-password" v-model="password">
        </div>
      </div>

      <!-- Footer -->
      <div class="modal-footer">
        <button class="btn btn-success loginButton" @click="login">Login</button>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'LoginView',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login: function () {
      this.$store.dispatch('login', {username: this.username, password: this.password})
    }
  }
}
</script>

<style scoped>
.modal {
  background-image: url("~@/assets/img/bg.jpg");
  background-size: cover;
}

.modal-header {
  display: flex;
  justify-content: center;
  padding-bottom: 0;
}

.modal-header img {
  width: 150px;
  height: 150px;
}

.loginButton {
  width: 100%;
}
</style>



