<template>
  <div class="toastPanel">
    <transition-group name="list">
      <div v-for="(toast, index) in toastList" class="toast toast-item" :key="toast.id" >
        <button class="btn btn-clear float-right" @click="close(index)"></button>
        <h6 v-if="toast.title">Title</h6>
        {{toast.message}}
        <div class="bar bar-sm">
          <div class="bar-item" :style="{width: toast.value+'%'}"></div>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: 'Toast',
  data () {
    return {
      toastList: []
    }
  },
  props: {
    timeout: {
      type: Number,
      default: 10 // Toast Timeout in Sec
    }
  },
  created: function () {
    window.console.toast = this.makeToast
  },
  methods: {
    makeId: function () {
      return Math.random().toString(36).substring(2)
    },
    makeToast: function (message, title) {
      var t = { title: title, message: message, id: this.makeId(), value: 100 }
      this.toastList.push(t)

      // Automatic Toast timeout timer
      var intervalHandel = setInterval(function () {
        var selectedIndex = this.toastList.indexOf(t)
        if (selectedIndex === -1) {
          // Toast has been closed by the user
          clearInterval(intervalHandel)
          return
        }
        var selectedToast = this.toastList[selectedIndex]
        selectedToast.value -= 1
        if (selectedToast.value <= 0) {
          clearInterval(intervalHandel)
          this.close(selectedIndex)
        }
      }.bind(this), this.timeout * 1000 / 100)
      // Divide total ms by 100% = n ms / tick
    },
    close: function (index) {
      this.toastList.splice(index, 1)
    }
  }
}
</script>

<style scoped>

.toastPanel{
  display: block;
  position: absolute;
  right: 10px;
  top: 0;
  width: 320px;
  z-index: 500;

  -webkit-transition: all 0.2s; /* For Safari 3.1 to 6.0 */
  transition: all 0.2s;
}

.toast {
  margin-top: 10px;
}

.list-enter-active, .list-leave-active {
  transition: all 1s;
}

.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateX(30px);
}

.bar-item {
  /* to smooth out the animation */
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  background-color: #48dbfb;
  left: 0;
}
</style>



