<template>
  <div class="frameview">

    <div class="empty"
      v-if="!$store.getters.isOnline && params.internet"
    >
      <div class="empty-icon">
        <i class="icon icon-cross"></i>
      </div>
      <p class="empty-title h5">This App require an Internet Connection</p>
      <p class="empty-subtitle">Please make sure your computer is connected to the Internet</p>
    </div>

    <iframe
    :src="params.url"
    width="100%"
    height="100%"
    frameBorder="0"
    v-if="!(!$store.getters.isOnline && params.internet)"
    :ref="AppUid"
    @load="onLoad"
    ></iframe>
  </div>

</template>

<script>
export default {
  name: 'IframeApp',
  data () {
    return {
      AppUid: console.makeId()
    }
  },
  computed: {
    listenerUid: function () {
      return `app-${this.AppUid}`
    }
  },
  props: {
    params: {},
    loaded: false
  },
  methods: {
    onLoad: function () {
      var iframe = this.$refs[this.AppUid]

      // this.$store.dispatch('mqttSubscribe', {topic: '/console/rfid', appUid: this.AppUid})
      // setup the listener for sending messages to this app
      // console.log('creating new event ' + this.listenerUid)
      window.addEventListener(this.listenerUid, function (e) {
        iframe.contentWindow.postMessage({
          event: e.name,
          content: e.data.content
        }, '*')
      }, false)
      // init the app
      iframe.contentWindow.postMessage({
        event: 'Onion.CDK.Init',
        appUid: this.AppUid
      }, '*')
    }
  },
  created: function () {
    console.log('created')
  }
}
</script>

<style scoped>
.frameview{
  flex: 1%;
  height: 100%;
  overflow: hidden;
}

.empty{
  height: 100%;
}

.empty-icon i{
  font-size: 2em;
  font-weight: bold;
  color: white;
  background-color: #f39c12;
  padding: 30px;
  border-radius: 50%;
}

iframe{
  margin: 0;
}
</style>



