<template>
  <div class="taskbar barAlign" v-bind:class="{ solidBg: solidBg }">

    <div class="taskBarStart barAlign">
      <div class="starticon c-hand" @click="onHome">
        <img src="static/img/icons/onion.svg" alt="">
      </div>
    </div>

    <div class="taskBarBody barAlign">
      <div v-for="app in $store.getters.runningApps"
        @click="openApp(app.id)"
        @mouseover="hoverApp = app.id"
        @mouseout="hoverApp = ''"
        :key="app.id"
        class="appIconContainer tooltip tooltip-dir" :data-tooltip="app.name" >
        <img :src="app.icon" class="appIcon">

        <i class="icon icon-cross closeBtn c-hand"
          v-show="hoverApp === app.id"
          @click="closeApp(app.id)"
        ></i>
      </div>
    </div>

    <div class="taskBarEnd barAlign">

      <div class="tooltip tooltip-dir"
        data-tooltip="No Internet. Some Apps may not work"
        v-if="!$store.getters.isOnline"
      >
        <i class="icon icon-stop no-wifi-icon" ></i>
        <!-- <i class="mdi mdi-wifi-off taskbar-icon no-wifi-icon"></i> -->
      </div>

      <div class="tooltip tooltip-dir"
        data-tooltip="About"
        @click="about"
      >
        <i class="icon icon-message taskbar-icon c-hand" ></i>
        <!-- <i class="mdi mdi-information-outline taskbar-icon c-hand"></i> -->
      </div>
      <div class="tooltip tooltip-dir"
        data-tooltip="Logout"
        @click="logout"
      >
        <i class="icon icon-shutdown taskbar-icon c-hand"></i>
        <!-- <i class="mdi mdi-power taskbar-icon c-hand"></i> -->

      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'TaskBar',
  props: {
    solidBg: true
  },
  data () {
    return {
      hoverApp: ''
    }
  },
  methods: {
    onHome: function () {
      this.$store.dispatch('launchApp', {appId: 'launcher'})
    },
    openApp: function (appId) {
      this.$store.dispatch('launchApp', {appId: appId})
    },
    closeApp: function (appId) {
      console.log(appId)
      this.$store.dispatch('closeApp', {appId: appId})
      window.event.cancelBubble = 'true'
      this.hoverApp = ''
    },
    logout: function () {
      // console.log('logout')
      this.$store.dispatch('logout', {})
    },
    about: function () {
      this.$emit('about')
    }
  }
}
</script>

<style scoped>
.closeBtn{
  position: absolute;
  bottom: -2px;
  right: -10px;
  color: white;
  background-color: #e74c3c;
  border-radius: 50%;
  transition: all 0.2s;
}

.barAlign {
  display: flex;
  flex-direction:column;
  align-self: center;
  align-content: center;
}

.taskBarBody { flex: 1; }

.taskbar-icon {
  color: white;
  font-size: 1em;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

.taskbar-icon:hover {
  color: #e74c3c;
}

.starticon {
  width: 28px;
  height: 28px;
  padding: 3px;
  background-color: white;
  border-radius: 50%;

  -webkit-transition: all 0.2s; /* For Safari 3.1 to 6.0 */
  transition: all 0.2s;
}

.starticon:hover {
  padding: 1px;
  border-radius: 25%;
}

.appIconContainer {
  width: 30px;
  height: 30px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.appIconContainer:hover {
  width: 42px;
  height: 42px;
}

.appIcon {
  width: 100%;
  height: 100%;

  padding: 10%;
}

.solidBg {
  background-color: black;
}

.no-wifi-icon {
  color: #f39c12;
  animation: blinker 10s linear infinite;
}
@keyframes blinker {
  0% { opacity: 1; }
  5% { opacity: 0; }
  10% { opacity: 1; }
  15% { opacity: 1; }
  20% { opacity: 0; }
  25% { opacity: 1; }
  30% { opacity: 1; }
  35% { opacity: 0; }
  40% { opacity: 1; }
}

@media screen and (min-width:768px){
  .taskbar{
    height: 100vh;
    width: 36px;
    padding-top: 20px;
    padding-bottom: 12px;
  }

  .barAlign > div{
    margin-bottom: 8px;
  }

  .tooltip.tooltip-dir::after {
    bottom: 50%;
    left: 100%;
    transform: translate(20px,50%);
  }
}

@media screen and (max-width:768px){
  .taskbar{
    flex-direction:row;
    height: 36px;
    width: 100%;

    /* padding-top: 0; */
    /* padding-bottom: 0; */
    padding-left: 20px;
    padding-right: 12px;
  }

  .barAlign {
    display: flex;
    flex-direction: row;
  }

  .barAlign > div{
    margin-right: 8px;
    align-content: center;
  }

  .tooltip.tooltip-dir::after {
    bottom: auto;
    top: 100%;
    transform: translate(-50%,.4rem);
  }
}
</style>



