<template>
  <div class="modal modal-sm" :class="{active: active}">
  <a class="modal-overlay" @click="close" ></a>
  <div class="modal-container">
    <div class="modal-header">
      <a class="btn btn-clear float-right" @click="close"></a>
      <!-- <div class="modal-title h5">About</div> -->
    </div>
    <div class="modal-body">
      <div class="content">
        <!-- content here -->
        <div class="about-image">
          <img src="static/img/OnionOS.svg" alt="">
        </div>

        <div class="about-text">
          OS version: 1.0
          <br>
          Kernel: {{$store.getters.deviceInfo.kernel}}
          <br>
          <br>
          Device: {{$store.getters.deviceInfo.hostname}}
          <br>
          Model: {{$store.getters.deviceInfo.model}}
        </div>

      </div>
    </div>
    <div class="modal-footer">
      &copy; 2018 Onion Corpration All Rights Reserved.
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'About',
  props: {
    active: true
  },
  methods: {
    close () {
      this.$emit('close')
    }
  }
}
</script>

<style scoped>

.about-text {
  text-align: center;
  font-size: 0.8em;
}

.about-image {
  display: flex;
  justify-content: center;
}

.about-image img {
  width: 200px;
  height: 200px;
}

.modal-footer {
  text-align: center;
  color: #bdc3c7;
  font-size: 0.5em;
}

</style>



