import Vue from 'vue'
// import '@mdi/font/css/materialdesignicons.min.css'
import 'spectre.css/dist/spectre.min.css'
import 'spectre.css/dist/spectre-icons.min.css'
// import 'spectre.css/dist/sspectre-exp.min.css'
import VueMqtt from 'vue-mqtt'

import store from './store'
import App from './App'

import VueAnalytics from 'vue-analytics'

// import { Analytics } from 'aws-amplify'
// import AwsExports from './app-config/aws-exports'
// Analytics.configure(AwsExports)

Vue.config.productionTip = false

var devMode = process.env.NODE_ENV === 'development'
var devHost = process.env.DEV_HOST

Vue.use(VueMqtt, `ws://${devMode ? devHost : window.location.hostname}:9001`)
Vue.use(VueAnalytics, {id: 'UA-48382427-15'})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  components: { App },
  template: '<App/>'
})



